
.card{
    width: 80vw;
    height: 14vh;
    background-color: #3e0942;
    color: rgb(76, 200, 238);
    border-radius: 8px;
    display: grid;
    grid-template-columns: 2fr 1fr 5fr;
    place-items: center;
    position: relative;
    margin: 12px;
}
.card .way{
    display: grid;
    place-items: center;
    width: 28px;
    height: 28px;
    border-radius: 14px;
    background-color: #9fa39f;
}
