.card .title{
    justify-self: left;
    padding-left: 30px;
}
.card .time{
    justify-self: left;
}
.way0{
    display: grid;
    content:'';
    position: absolute;
    align-self: baseline;
    z-index: 10;
    width: 20px;
    height:0%;
    background-color:#9fa39f;
    margin-top: 10px;
    border-radius: 10px;
    animation: going 3s infinite;
}
@keyframes going{
    0%{
        height: 0%;
    }
    25%{
        height: 174%;
    }
    50%{
        height: 348%;
    }
    75%{
        height: 174%;
    }
    100%{
        height: 0%;
    }
    

}